import { Button, Image, Text } from '@atoms';
import { CustomTheme } from '@theme';
import React from 'react';
import { ImageStyle, ImageURISource, StyleProp, TextStyle, View, ViewStyle } from 'react-native';
import { useTheme } from 'react-native-paper';
import { IButtonProps } from '../Button';
import makeStyles from './css';

interface IProps {
    rootStyle?: StyleProp<ViewStyle>;
    buttonProps?: IButtonProps;
    icon: ImageURISource;
    iconStyle?: StyleProp<ImageStyle> | undefined;
    label?: string;
    children?: React.ReactNode;
    childrenContainerStyle?: StyleProp<ViewStyle>;
}

const IconButton = (props: IProps) => {
    const theme = useTheme();
    const styles = makeStyles(theme as CustomTheme);

    const rtlStyle: StyleProp<TextStyle> = { direction: 'rtl' };

    return (
        <View style={[styles.root, props.rootStyle]}>
            <Button {...props.buttonProps}
                labelStyle={[props.buttonProps?.labelStyle, styles.buttonLabel]}>
                <View style={[styles.buttonContentContainer, props.childrenContainerStyle, rtlStyle]}>
                    <Image source={props.icon} style={[styles.iconStyle, props.iconStyle]} />
                    {
                        !!props.label &&
                        <Text>props.label</Text>
                    }
                    {props.children}
                </View>
            </Button>
        </View>
    );
};
export default IconButton;
