import { CustomTheme } from '@theme';
import React, { Ref } from 'react';
import { AccessibilityRole, LayoutChangeEvent, StyleProp, TextStyle, View, ViewStyle } from 'react-native';
import { Button as PaperButton, useTheme } from 'react-native-paper';
import makeStyles from './css';

interface IProps {
    mode?: "text" | "outlined" | "contained" | undefined;
    disabled?: boolean | undefined;
    children?: React.ReactNode;
    uppercase?: boolean | undefined;
    onPress?: (() => void) | undefined;
    onLongPress?: (() => void) | undefined;
    primary?: boolean | undefined;
    rootStyle?: StyleProp<ViewStyle>;
    contentStyle?: StyleProp<ViewStyle>;
    style?: StyleProp<ViewStyle>;
    labelStyle?: StyleProp<TextStyle>;
    onLayout?: ((event: LayoutChangeEvent) => void) | undefined;
    roundness?: number;

    accessible?: boolean | undefined;
    accessibilityRole?: AccessibilityRole | undefined;
    accessibilityLabel?: string | undefined;
    accessibilityHint?: string | undefined;
};

const Button = React.forwardRef((props: IProps, ref?: Ref<View>) => {
    const uppercase = props.uppercase ?? false;
    const primary = props.primary ?? false;

    const theme = useTheme();
    const styles = makeStyles(theme as CustomTheme);

    return (
        <View ref={ref} style={[styles.root, props.rootStyle]}>
            <PaperButton {...props}
                theme={props.roundness || props.roundness === 0 ? { roundness: props.roundness } : {}}
                color={theme.colors[primary ? 'primary' : 'accent']}
                uppercase={uppercase}
                contentStyle={[styles.content, props.contentStyle]}
                style={props.style}
                labelStyle={[styles.label, { color: theme.colors[primary ? 'text' : 'primary'] }, props.labelStyle]}>
            </PaperButton>
        </View>
    );
});
export default Button;
export { IProps as IButtonProps };

